body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg{
    background-image: -webkit-linear-gradient(left, #ec6a00, #dd4620);
    background-image: linear-gradient(to right, #ec6a00, #dd4620);
    color: white;
}

.exit {
    font-size: 1.8rem;
    padding-top: .8rem;
}
body{
    font-size: 20px;
}

.buttons{
    width: 100%;
    padding: 1.5rem;
    margin: .7rem;
}

.buttonsMenu{
    width: 40%;
    padding: 1.5rem;
    margin: .7rem;
    height: 8.2rem;
    white-space: pre-wrap;
    background-color: rgb(218, 222, 238);
}
.mar {
    margin-top: 3rem;
}


.order{
    font-size: 3rem;
}
.fontSize{
    font-size: 2rem;
}
.pink {
    background-image: -webkit-linear-gradient(left, #ec6a00, #dd4620);
    background-image: linear-gradient(to right, #ec6a00, #dd4620);
    color: white;
    padding: 1rem;
    width: 100%;
    margin-top: 1rem;
    height: 6rem;
}

.social {
    padding: 1rem;
    width: 100%;
    margin-top: 1rem;
}

.inputs {
    height: 6rem;
}

.all{
    display: flex;
    flex-direction: column;
}

.logo{
    height: 60vmin;
    align-content: center;
    justify-content: center;
}

.lessMg{
    margin-top: -4rem;
}

.pass{
    color: #dd4620;
    font-size: 2rem; 
    text-align: right;
    padding: 3rem;
}

